import {action} from '../axios'

export function index_getAllNum() {
  return action({
    method: 'post',
    url: '/APIHome/GetAllNum',
  })
}

export function index_getBuyHistory({
  params
}) {
  const {pageIndex, pageSize = 5} = params
  return action({
    method: 'post',
    url: '/APIHome/GetHisOrders',
    params: {
      pageIndex,
      pageSize
    }
  })
}

export function index_getNewsList() {
  return action({
    method: 'post',
    url: '/APIHome/GetArticleTitle'
  })
}

export function index_getNewsDetail({
  params
}) {
  const {id} = params
  return action({
    method: 'post',
    url: '/APIHome/GetArticleContent',
    params: {
      id
    }
  })
}



