<template>
  <div class="sidebarct">
    <Menu @on-select="selectMenu" ref="Menu" theme="light" class="menuct" :active-name="active" :open-names="openname" width="auto">
      <Submenu name="1">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-home"></Icon>
          
        </template>
        <MenuItem name="1-1"></MenuItem>
        <MenuItem name="1-2"></MenuItem>
        <MenuItem name="1-3"></MenuItem>
      </Submenu>
      <Submenu name="2">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-cart"></Icon>
          
        </template>
        <MenuItem name="2-1"></MenuItem>
        <MenuItem name="2-2"></MenuItem>
      </Submenu>
      <Submenu name="3">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-list"></Icon>
          
        </template>
        <MenuItem name="3-1"></MenuItem>
        <MenuItem name="3-2"></MenuItem>
        <MenuItem name="3-3"></MenuItem>
        <MenuItem name="3-4"></MenuItem>
      </Submenu>
      <Submenu name="4">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-printer"></Icon>
          
        </template>
        <MenuItem name="4-1"></MenuItem>
        <MenuItem name="4-2"></MenuItem>
      </Submenu>
      <Submenu name="5">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-paper"></Icon>
          
        </template>
        <MenuItem name="5-1"></MenuItem>
        <MenuItem name="5-2"></MenuItem>
        <MenuItem name="5-3"></MenuItem>
      </Submenu>
      <Submenu name="6">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-filing"></Icon>
          
        </template>
        <MenuItem name="6-1"></MenuItem>
        <MenuItem name="6-2"></MenuItem>
        <MenuItem name="6-3"></MenuItem>
        <MenuItem name="6-4"></MenuItem>
        <MenuItem name="6-5"></MenuItem>
      </Submenu>
      <Submenu name="7">
        <template slot="title">
          <Icon class="sidebaricon" type="ios-folder"></Icon>
          
        </template>
        <MenuItem name="7-1"></MenuItem>
        <MenuItem name="7-2"></MenuItem>
        <MenuItem name="7-3"></MenuItem>
        <MenuItem name="7-4"></MenuItem>
        <MenuItem name="7-5"></MenuItem>
      </Submenu>
    </Menu>
  </div>
</template>
<script>
  import vue from 'vue'
  const routeMap = [
    {
      name: '1',
      title: '',
      children: [
        {
          name: '1-1',
          title: ''
        },{
          name: '1-2',
          title: ''
        },{
          name: '1-3',
          title: ''
        }
      ]
    },{
      name: '2',
      title: '',
      children: [
        {
          name: '2-1',
          title: ''
        },{
          name: '2-2',
          title: ''
        }
      ]
    },{
      name: '3',
      title: '',
      children: [
        {
          name: '3-1',
          title: ''
        },{
          name: '3-2',
          title: ''
        },{
          name: '3-3',
          title: ''
        },{
          name: '3-4',
          title: ''
        }
      ]
    },{
      name: '4',
      title: '',
      children: [
        {
          name: '4-1',
          title: ''
        },{
          name: '4-2',
          title: ''
        }
      ]
    },{
      name: '5',
      title: '',
      children: [
        {
          name: '5-1',
          title: ''
        },{
          name: '5-2',
          title: ''
        },{
          name: '5-3',
          title: ''
        }
      ]
    },{
      name: '6',
      title: '',
      children: [
        {
          name: '6-1',
          title: ''
        },{
          name: '6-2',
          title: ''
        },{
          name: '6-3',
          title: ''
        },{
          name: '6-4',
          title: ''
        },{
          name: '6-5',
          title: ''
        }
      ]
    },{
      name: '7',
      title: '',
      children: [
        {
          name: '7-1',
          title: ''
        },{
          name: '7-2',
          title: ''
        },{
          name: '7-3',
          title: ''
        },{
          name: '7-4',
          title: ''
        },{
          name: '7-5',
          title: ''
        }
      ]
    }
  ]
  export default {
    name: 'SideBar',
    data() {
      return {
        openname: [],
        active: ''
      }
    },
    mounted() {
      this.init()
    },
    methods: {
      selectMenu(name) {
        this.$router.push({name})
      },
      init() {
        this.active = this.$route.name
        this.openname.splice(0,5,this.$route.meta.menuOpen)
        this.$refs.Menu.updateActiveName();
        this.$refs.Menu.updateOpened();
      }
    }
  }

</script>

<style lang="less" scoped>
  .sidebarct {
    background-color: #fff;
    height: 100%;
  }
  .sidebaricon {
    font-size: 20px;
    position: relative;
    top: 2px;
  }
  .menuct {
    height: 100%;
  }
</style>



