<template>
  <div id="admin">
    <div id="sidebar">
      <SideBar ref="SideBar"></SideBar>
    </div>
    <div id="topbar">
      <TopBar></TopBar>
    </div>
    <main id="main">
      <div id="breadcrumb">
        <MkBreadcrumb></MkBreadcrumb>
      </div>
      <div style="height: 20px;"></div>
      <router-view/>
    </main>
  </div>
</template>
<script>
  import SideBar from './SideBar'
  import TopBar from './TopBar'
  import MkBreadcrumb from './MkBreadcrumb.vue'

  export default {
    name: 'Admin',
    components: {
      SideBar,
      TopBar,
      MkBreadcrumb
    },
    updated() {
      if (this.$refs.SideBar) {
        this.$refs.SideBar.init()
      }
    }
  }

</script>
<style lang="less" scoped>
  @import '../../theme/theme.less';

  #admin {
    position: relative;
    height: 100%;
    background-color: #f5f5f5
  }

  #sidebar {
    position: fixed;
    left: 0;
    top: 0;
    padding-top: @topbar;
    height: 100%;
    overflow: hidden;
    overflow-y: auto;
    width: @sidebar;
    z-index: 98;
    background: #fff;
  }

  #topbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: @topbar;
    z-index: 99;
  }

  #main {
    padding-left: @sidebar;
    padding-top: @topbar;
    background-color: #f5f5f5;
  }

  #breadcrumb {
    height: 50px;
    line-height: 50px;
    background-color: #fff;
    padding: 0 15px;
  }

</style>



