import {action} from '../axios'
import setToken from './setToken'

export function login({
  params
}) {
  return action({
    url: '/APILogin/GetToken',
    method: 'post',
    params,
    noToken: true
  }).then(res=> {
    const token = res.Msg
    setToken(token)
    return res
  })
}

export function getMsgTime_infoForm() {
  return action({
    url: '/APIMobileDynamicCode/GetPTime',
    method: 'post',
  })
}

export function getMsgTime_forgetForm() {
  return action({
    url: '/APIMobileDynamicCode/GetRTime',
    method: 'post',
  })
}

export function sendMsg_infoForm({
  mobile
}) {
  return action({
    url: '/APIMobileDynamicCode/PerfectInfo',
    method: 'post',
    params: {
      mobile
    }
  })
}

export function sendMsg_forgetForm({
  params
}) {
  const {user} = params
  return action({
    url: '/APIMobileDynamicCode/ResetPsw',
    method: 'post',
    noToken: true,
    params: {
      user
    }
  })
}

export function postInfo_infoForm({
  params
}) {
  const {mobile, password, vcode} = params
  return action({
    url: '/APIMember/UpdateTelandPsw',
    method: 'post',
    params: {
      mobile,
      password,
      vcode
    }
  })
}

export function postInfo_forgetForm({
  params
}) {
  const {
    user,
    password,
    tpassword,
    vcode
  } = params
  return action({
    url: '/APIMember/UpdatePsw',
    method: 'post',
    noToken: true,
    params: {
      user,
      password,
      tpassword,
      vcode
    }
  })
}



