import Vue from 'vue'
import Router from 'vue-router'
import Admin from '@/containers/Admin/Admin'
import Login from '@/containers/Login/Login'
import Index from '@/containers/Index/Index'
import FeedBack from '@/containers/FeedBack/FeedBack'
import News from '@/containers/News/News'

import ToOrder from '@/containers/ToOrder/ToOrder'
import MyCart from '@/containers/MyCart/MyCart'
import MyOrder from '@/containers/MyOrder/MyOrder'
import OrderReport from '@/containers/OrderReport/OrderReport'
import SearchOutput from '@/containers/SearchOutput/SearchOutput'
import ReturnGoods from '@/containers/ReturnGoods/ReturnGoods'
import NeedBill from '@/containers/NeedBill/NeedBill'
import MyBill from '@/containers/MyBill/MyBill'
import ReturnMoney from '@/containers/ReturnMoney/ReturnMoney'
import ClaimMoney from '@/containers/ClaimMoney/ClaimMoney'
import BillDetail from '@/containers/BillDetail/BillDetail'
import AccountInfo from '@/containers/AccountInfo/AccountInfo'
import CreditLine from '@/containers/CreditLine/CreditLine'
import UploadPapers from '@/containers/UploadPapers/UploadPapers'
import MyAddress from '@/containers/MyAddress/MyAddress'
import ChangePsw from '@/containers/ChangePsw/ChangePsw'

import setToken from '@/axios/actions/setToken'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/admin',
      name: 'Admin',
      component: Admin,
      beforeEnter: (to, from, next) => {
        const token = window.localStorage.getItem('token')
        const tokenDataStr = window.localStorage.getItem('tokenData')
        if (!token || !tokenDataStr) {
          return next('/')
        }
        const nowDate = new Date().getTime()/1000 + 600
        const tokenData = JSON.parse(tokenDataStr)
        if (tokenData.exp < nowDate) {
          return next('/')
        }
        setToken(token)
        return next()
      },
      children: [
        {
          path: 'index',
          component: Index,
          meta: {
            menuOpen: '1'
          },
          name: '1-1'
        },
        {
          path: 'feedback',
          component: FeedBack,
          meta: {
            menuOpen: '1'
          },
          name: '1-2'
        },
        {
          path: 'news',
          component: News,
          meta: {
            menuOpen: '1'
          },
          name: '1-3'
        },
        {
          path: 'toorder',
          component: ToOrder,
          meta: {
            menuOpen: '2'
          },
          name: '2-1'
        },
        {
          path: 'mycart',
          component: MyCart,
          meta: {
            menuOpen: '2'
          },
          name: '2-2'
        },
        {
          path: 'myorder',
          component: MyOrder,
          meta: {
            menuOpen: '3'
          },
          name: '3-1'
        },
        {
          path: 'orderreport',
          component: OrderReport,
          meta: {
            menuOpen: '3'
          },
          name: '3-2'
        },
        {
          path: 'searchoutput',
          component: SearchOutput,
          meta: {
            menuOpen: '3'
          },
          name: '3-3'
        },
        {
          path: 'returngoods',
          component: ReturnGoods,
          meta: {
            menuOpen: '3'
          },
          name: '3-4'
        },
        {
          path: 'needbill',
          component: NeedBill,
          meta: {
            menuOpen: '4'
          },
          name: '4-1'
        },
        {
          path: 'mybill',
          component: MyBill,
          meta: {
            menuOpen: '4'
          },
          name: '4-2'
        },
        {
          path: 'returnmoney',
          component: ReturnMoney,
          meta: {
            menuOpen: '5'
          },
          name: '5-1'
        },
        {
          path: 'claimmoney',
          component: ClaimMoney,
          meta: {
            menuOpen: '5'
          },
          name: '5-2'
        },
        {
          path: 'billdetail',
          component: BillDetail,
          meta: {
            menuOpen: '5'
          },
          name: '5-3'
        },
        {
          path: 'accountinfo',
          component: AccountInfo,
          meta: {
            menuOpen: '6'
          },
          name: '6-1'
        },
        {
          path: 'creditline',
          component: CreditLine,
          meta: {
            menuOpen: '6'
          },
          name: '6-2'
        },
        {
          path: 'uploadpapers',
          component: UploadPapers,
          meta: {
            menuOpen: '6'
          },
          name: '6-3'
        },
        {
          path: 'myaddress',
          component: MyAddress,
          meta: {
            menuOpen: '6'
          },
          name: '6-4'
        },
        {
          path: 'changepsw',
          component: ChangePsw,
          meta: {
            menuOpen: '6'
          },
          name: '6-5'
        }
      ]
    },
    {
      path: '/',
      name: 'Login',
      component: Login
    }
  ]
})



