<template>
  <div class="topbarct">
    <div class="title">
      <img :src="logo" alt="">
    </div>
    <div class="infoBox">
      {{clientName}}
      <span><i class="iconfont">&#xe684;</i></span>
      <span @click="toCart"><i class="iconfont">&#xe60c;</i></span>
      <span @click="logout"><i class="iconfont">&#xe606;</i></span>
    </div>
  </div>
</template>
<script>
import logout from '@/axios/actions/logout'
import logo from '@/assets/img/logo_white.png'
export default {
  name: 'TopBar',
  data() {
    return {
      clientName: window.$$tokenData.UserName,
      logo: logo
    }
  },
  methods: {
    logout() {
      logout()
    },
    toCart() {
      this.$router.push('mycart')
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../theme/theme.less';

.topbarct {
  height: 100%;
  background-color: #2C5879;
  padding: 0 30px;
  .title {
    font-size: 20px;
    color: #fff;
    font-weight: bold;
    line-height: @topbar;
    float: left;
    padding-top: 10px;
  }
  .infoBox {
    float: right;
    padding-top: 3px;
    color: #ddd;
    .iconfont {
      font-size: 30px;
      position: relative;
      top: 5px;
      margin-right: 5px;
    }
    span {
      margin-left: 12px;
      cursor: pointer;
      &:hover {
        color: #fff;
      }
      &:active {
        color: #999
      }
    }
  }
}
</style>



