<template>
  <div class="accountInfo">
    <Row>
      <Col :md="18">
        <Col :md="6">
        <router-link to="/" class="item">
          <img :src="imgs.img1" alt="">
          <div>
            ({{allNum.carts_num}})
          </div>
        </router-link>
        </Col>
        <Col :md="6">
        <router-link to="/" class="item">
          <img :src="imgs.img2" alt="">
          <div>
            ({{allNum.greceipt_num}})
          </div>
        </router-link>

        </Col>
        <Col :md="6">
        <router-link to="/" class="item">
          <img :src="imgs.img3" alt="">
          <div>
            ({{allNum.pay_num}})
          </div>
        </router-link>

        </Col>
        <Col :md="6">
        <router-link to="/" class="item">
          <img :src="imgs.img4" alt="">
          <div>
            ({{allNum.preceipt_num}})
          </div>
        </router-link>
        </Col>
      </Col>
      <Col :md="6"> 
      </Col>
    </Row>
  </div>
</template>
<script>
  import {
    mapActions
  } from 'vuex'
  import img1 from '@/assets/img/index/1.png'
  import img2 from '@/assets/img/index/2.png'
  import img3 from '@/assets/img/index/3.png'
  import img4 from '@/assets/img/index/4.png'

  export default {
    name: 'AccountInfo',
    data() {
      return {
        allNum: {
          carts_num: 0,
          greceipt_num: 0,
          pay_num: 0,
          preceipt_num: 0,
        },
        imgs: {
          img1,
          img2,
          img3,
          img4
        }
      }
    },
    mounted() {
      this.getAllNum()
    },
    methods: {
      ...mapActions(['action']),
      getAllNum() {
        this.action({
          module: 'index_getAllNum',
          api: 'index_getAllNum'
        }).then(res => {
          this.allNum = res.Msg
        })
      }
    }
  }

</script>
<style lang="less" scoped>
  .accountInfo {
    .item {
      display: inline-block;
      text-align: center;
      padding: 15px 0;
      font-size: 14px;
      img {
        margin-bottom: 10px;
        display: inline-block
      }
    }
      .ivu-col-span-md-6 {
        text-align: center
      }
  }

</style>



