<template>
  <div class="newsList">
    <li v-for="item in data" class="clearBoth" @click="openNews(item)">
      <div class="circleIcon"></div>
      <div class="articleTitle">
         {{item.ArticleTitle}} 
      </div>
      <div class="articleTime">
        {{item.AnnounceShortTime}}
      </div>
    </li>
    <Modal
      v-model="modal"
      width="800"
     >
     <h2 slot="header">{{detail.title}} <small>{{detail.time}}</small></h2>
     <div class="articleDetail">
      <div class="articleContent" v-html="detail.content"></div>
     </div>
      
    </Modal>
  </div>
</template>
<script>
export default {
  name: 'NewsList',
  mounted(){
    this.getNewsList()
  },
  data() {
    return {
      data: [],
      modal: false,
        detail: {
          title: '',
          time: '',
          content: ''
        }
    }
  },
  methods: {
    getNewsList() {
      window.$$action({
        module: 'index_getNewsList',
        api: 'index_getNewsList'
      }).then(res => {
        this.data = res.Msg
        console.log(res)
      })
    },
    openNews(item) {
        window.$$action({
          module: 'index_getNewsDetail',
          api: 'index_getNewsDetail',
          params: {
            id: item.id
          }
        }).then(res => {
          this.detail.title = res.Msg.ArticleTitle
          this.detail.time = res.Msg.AnnounceTime
          this.detail.content = res.Msg.ArticleContent
          this.modal = true
        })
      }
  }
}
</script>
<style lang="less">
.articleContent {
  img {
    max-width: 100%;
  }
}
</style>

<style lang="less" scoped>
.articleDetail {
  padding: 20px 30px;
}

.newsList {
  padding: 10px;
  font-size: 14px;
  .circleIcon {
    float: left;
    width: 10px;
    height: 10px;
    background-color: #999;
    border-radius: 5px;
    position: relative;
    top: 6px;
    margin-right: 8px;
  }
  .articleTitle {
    float: left;
  }
  .articleTime {
    float: right;
  }
  li {
    margin: 3px 0;
    list-style: none;
    cursor: pointer;
    &:hover {
      color: #999
    }
  }
}
</style>




