import {action} from '../axios'
import jwt from 'jsonwebtoken'

export default function testJwt() {
  return action({
    actionName: 'testJwt',
    method: 'post',
    url: '/APILogin/DesToken',
    headers: {
      "Authorization": `${window.localStorage.getItem('token')}`
    },
    params: {
      test: true
    }
  })
}


