<template>
  <div class="table">
    <table class="mk-table text-center">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th>()</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in data">
          <td>{{item.OrderDate}}</td>
          <td>{{item.OrderCode}}</td>
          <td>{{item.OStatus | status }}</td>
          <td>{{item.ShopName}}</td>
          <td>{{item.Qty}}</td>
          <td>{{item.NeedPayAmount}}</td>
          <td>{{item.CSGName}}</td>
          <td>{{item.CSGMobile}}</td>
          <td>
            <a href=""></a>
            <a href=""></a>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="page">
      <Page :total="total" :page-size="5" @on-change="changePage" ></Page>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'BuyHistory',
    data() {
      return {
        data: [],
        total: 0
      }
    },
    filters: {
      status(data) {
        const map = {
          '$0': '',
          '$1': '',
          '$2': '',
          '$3': '',
          '$-2': '',
          '$-6': ''
        }
        // --0:;1:;2:;3:;-2;-6:"
        return map[`$${data}`]
      }
    },
    mounted() {
      this.getData()
    },
    methods: {
      getData(page) {
        return window.$$action({
          module: 'index_getBuyHistory',
          api: 'index_getBuyHistory',
          params: {
            pageIndex: page||0
          }
        }).then(res => {
          this.data = res.Msg
          if (this.data && this.data[0]) {
            this.total = res.Msg[0].Rows/1
          }
        })
      },
      changePage(page) {
        this.getData(page-1)
      }
    }
  }

</script>
<style lang="less" scoped>
  .table {
    padding: 20px 10px;
    .page {
      padding: 10px 20px;
      text-align: right
    }
  }

</style>



