<template>
  <div v-if="show" class="globalProgress" style="position: fixed; top:-8px;z-index:9999;left:0;right:0">
    <Progress :stroke-width="3" :percent="percent" hide-info :status="status"></Progress>
  </div>
</template>
<script>
export default {
  name: 'GlobalProgress',
  data() {
    return {
      percent: 0,
      show: false,
      intervalBox: null,
      status: 'active'
    }
  },
  methods: {
    start () {
      this.status = 'active'
      this.percent = 0
      this.show = true
      let remain = 100
      this.intervalBox = setInterval(() => {
        remain = remain * (0.5 + Math.random()*0.5)
        this.percent = 100 - remain
      }, 1000)
    },
    finish () {
      clearInterval(this.intervalBox)
      this.percent = 100
      setTimeout(() => {
        this.show = false
      }, 1000)
    },
    wrong() {
      clearInterval(this.intervalBox)
      this.percent = 99.99
      this.status = 'wrong'
      setTimeout(() => {
        this.show = false
      }, 2000)
    }
  }
}
</script>

<style lang="less">
.globalProgress {
  .ivu-progress-inner {
    background: transparent
  }
}
</style>



