<template>
  <div id="indexct">
    <Row :gutter="30">
      <Col :md="6">
        <div class="userInfo">
          <UserInfo></UserInfo>
        </div>
      </Col>
      <Col :md="18">
        <div class="accountInfo">
          <AccountInfo></AccountInfo>
        </div>
      </Col>
    </Row>
    <div style="height: 20px;"></div>
    <Row :gutter="30">
      <Col :md="18">
        <div class="orderTableBox">
          <TitleLink name="()" link="/"></TitleLink>
          <OrderTable></OrderTable>
        </div>
      </Col>
      <Col :md="6">
        <div class="newsBox">
          <TitleLink name="" link="/admin/news"></TitleLink>
          <NewsList></NewsList>
        </div>
        <div style="height: 20px"></div>
        <div class="moreLink">
          <router-link to='/'>
            
          </router-link>
        </div>
        <div style="height: 20px"></div>
        <div class="moreLink">
          <router-link to='/'>
            
          </router-link>
        </div>
      </Col>
    </Row>
    <div style="height: 20px"></div>
    <Row>
      <div class="buyHistoryBox">
        <TitleLink name="" link="/"></TitleLink>
        <BuyHistory></BuyHistory>
      </div>
    </Row>
    <div style="height: 20px"></div>
    <Row>
      <div class="noticeBox">
        <TitleLink name="" link="/"></TitleLink>
        <NoticeTable></NoticeTable>
      </div>
    </Row>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'

import TitleLink from '@/components/TitleLink'

import UserInfo from './UserInfo'
import AccountInfo from './AccountInfo'
import OrderTable from './OrderTable'
import NewsList from './NewsList'
import BuyHistory from './BuyHistory'
import NoticeTable from './NoticeTable'

export default {
  components: {
    UserInfo,
    AccountInfo,
    OrderTable,
    TitleLink,
    NewsList,
    BuyHistory,
    NoticeTable
  },
  name: 'Index',
  computed: {
    ...mapGetters(['store'])
  },
  methods: {
    ...mapActions(['action']),
    login() {
      this.action({
        module: 'login',
        api: 'login'
      })
      console.log(this)
    },
    testJwt() {
      this.action({
        module: 'testJwt',
        api: 'testJwt'
      })
    }
  }
}
</script>
<style lang="less" scoped>
#indexct {
  padding: 0 15px 30px;
}
.userInfo {
  height: 150px;
  background: #fff;
}
.accountInfo {
  height: 150px;
  background: #fff;
}
.orderTableBox {
  height: 500px;
  background: #fff;
}
.newsBox {
  height: 300px;
  background: #fff;
}
.moreLink {
  height: 80px;
  background: #fff;

}
.buyHistoryBox {
  background: #fff;
}
.noticeBox {
  background: #fff;
  height: 300px;
}
</style>




