<template>
  <div class="userinfo">
    <Row>
      <Col :md="8">
      <div class="imgBox">
        <i class="iconfont">&#xe632;</i>
      </div>
      </Col>
      <Col :md="16">
      <div class="infoBox">
        <h3>{{company}}</h3>
        <p>
           
        </p>
      </div>
      </Col>
    </Row>
  </div>
</template>
<script>
  export default {
    name: 'UserInfo',
    data() {
      return {
        company: window.$$tokenData.UserName
      }
    }
  }

</script>
<style lang="less" scoped>
  .userinfo {
    padding: 30px 0;
    .imgBox {
      width: 88px;
      height: 88px;
      border: 2px dashed #ddd;
      border-radius: 50px;
      text-align: center;
      padding-top: 5px;
      transition: all 0.3s ease;
      .iconfont {
        font-size: 50px;
        color: #999;
        user-select: none;
      }
      &:hover {
        border: 2px solid #999;
        .iconfont {
          color: #666
        }
      }
    }
    .infoBox {
      float: right;
      padding-top: 20px;
    }
  }

</style>



