<template>
  <div class="loginContainer" :style="{background: `url(${backgroundImg}) center/cover no-repeat`}">
    <div class="loginBox">
      <h3 class="logoBox">
        <img :src="logo" alt="">
      </h3>
      <div class="inputBox">
        <i class="iconfont">&#xe632;</i>
        <input placeholder="" class="input" v-model="userName">
      </div>
      <div class="inputBox">
        <i class="iconfont">&#xe6ef;</i>
        <input placeholder="" class="input" v-model="password" type="password">
      </div>
      <div class="verifyImgBox clearBoth">
        <input placeholder="" class="verifyInput" v-model="verifyCode" type="text">
        <img class="verifyImg" :src="verifyImgUrl" alt="">
        <span @click="refreshVerify" class="refresh">?</span>
      </div>
      <div class="forgetPsw">
        <a @click="openForgetPsw" href="javascript:void(0)">
          <i class="iconfont">&#xe682;</i></a>
      </div>
      <div class="buttonBox">
        <Button class="login" type="primary" @click="login"></Button>
      </div>
    </div>
    <footer class="footer">
      <div style="float: left">
        <h2 style="margin-bottom: 10px;"></h2>
        <p style="line-height: 25px;">
          299
          <br> 315104
          <br> 
          <span style="margin-right: 10px;">86-0574-88223000</span>
          <span style="margin-right: 10px;">86-0574-88223111</span> 86-0574-88231889
        </p>
      </div>
      <div style="float: right;width: 240px;">
        <img :src="qrcode" alt="">
        <h2 style="float: right;line-height: 70px;"></h2>
      </div>
    </footer>
    <Modal width="400" v-model="infoModal" title="" :closable="false" :mask-closable="false">
      <div class="formModal">
        <div class="formItem">
          <div class="title">
            
          </div>
          <div class="inputItem">
            <Input v-model="infoForm.phone"></Input>
          </div>
        </div>
        <div class="formItem">
          <div class="title">
            
          </div>
          <div class="inputItem">
            <Input type="password" v-model="infoForm.newPsw"></Input>
          </div>
        </div>
        <div class="formItem">
          <div class="title">
            
          </div>
          <div class="inputItem">
            <Input v-model="infoForm.verifyMsg" class="input"></Input>
            <Button type="primary" @click="sendMsg_infoForm" :disabled="infoForm.sendMsg != ''" class="button">{{infoForm.sendMsg}}</Button>
          </div>
        </div>
      </div>
      <div slot="footer" class="formFooter">
        <Button size="large" @click="postInfo" long type="success"></Button>
        <div style="text-align:right">
          <Button @click="skip" size="small" style="color: #aaa" type="text"></Button>
        </div>
      </div>
    </Modal>
    <Modal width="400" v-model="forgetPswModal" title="">
      <div class="formModal">
        <div class="formItem">
          <div class="title">
            
          </div>
          <div class="inputItem">
            <Input v-model="forgetForm.user"></Input>
          </div>
        </div>
        <div class="formItem">
          <div class="title">
            
          </div>
          <div class="inputItem">
            <Input v-model="forgetForm.password" type="password"></Input>
          </div>
        </div>
        <div class="formItem">
          <div class="title">
            
          </div>
          <div class="inputItem">
            <Input v-model="forgetForm.tpassword" type="password"></Input>
          </div>
        </div>
        <div class="formItem">
          <div class="title">
            
          </div>
          <div class="inputItem">
            <Input v-model="forgetForm.vcode" class="input"></Input>
            <Button :disabled="infoForm.sendMsg != ''" @click="sendMsg_forgetForm" type="primary" class="button">{{forgetForm.sendMsg}}</Button>
          </div>
        </div>
      </div>
      <div slot="footer" class="formFooter">
        <Button @click="postInfo_forgetForm" size="large" long type="success"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import {
    mapActions
  } from 'vuex'
  import backgroundImg from '@/assets/img/bg_login.png'
  import logo from '@/assets/img/img_logo.png'
  import qrcode from '@/assets/img/img_erweima.png'
  export default {
    name: 'Login',
    data() {
      return {
        userName: '',
        password: '',
        verifyImgUrl: `${window.$$baseURL}/APILogin/ValidateCode?${new Date().getTime()}`,
        verifyCode: '',
        backgroundImg: backgroundImg,
        logo: logo,
        qrcode: qrcode,
        infoModal: false,
        infoForm: {
          phone: '',
          newPsw: '',
          verifyMsg: '',
          sendMsg: '',
          sendInterval: null
        },
        forgetPswModal: false,
        forgetForm: {
          user: '',
          password: '',
          tpassword: '',
          vcode: '',
          sendMsg: '',
          sendInterval: null
        }
      }
    },
    methods: {
      ...mapActions(['action']),
      openForgetPsw() {
        this.forgetPswModal = true
        // this.action({
        //   module: 'Login_getMsgTime_forgetForm',
        //   api: 'getMsgTime_forgetForm',
        // }).then(res => {
        //   console.log(res)
        // })
      },
      postInfo_forgetForm() {
        if (!this.forgetForm.vcode) {
          return this.$Notice.error({
            title: ''
          })
        }
        this.action({
          module: 'Login_postInfo_forgetForm',
          api: 'postInfo_forgetForm',
          params: {
            user: this.forgetForm.user,
            password: this.forgetForm.password,
            tpassword: this.forgetForm.tpassword,
            vcode: this.forgetForm.vcode
          }
        }).then(res => {
          this.forgetPswModal = false
          this.userName = this.forgetForm.user
          this.password = this.forgetForm.password
          this.forgetForm = {
            user: '',
            password: '',
            tpassword: '',
            vcode: '',
            sendMsg: '',
            sendInterval: null
          }
          this.$Notice.success({
            title: ''
          })
        })
      },
      postInfo() {
        this.action({
          module: 'Login_postInfo',
          api: 'postInfo_infoForm',
          params: {
            mobile: this.infoForm.phone,
            password: this.infoForm.newPsw,
            vcode: this.infoForm.verifyMsg
          }
        }).then(res => {
          this.$Notice.success({
            title: ''
          })
          this.skip()
        })
      },
      sendMsg_forgetForm() {
        if (!this.forgetForm.user) {
          return this.$Notice.error({
            title: ''
          })
        }
        if (!this.forgetForm.password) {
          return this.$Notice.error({
            title: ''
          })
        }
        if (!this.forgetForm.tpassword) {
          return this.$Notice.error({
            title: ''
          })
        }
        this.action({
          module: 'sendMsg_forgetForm',
          api: 'sendMsg_forgetForm',
          params: {
            user: this.forgetForm.user
          }
        }).then(res => {
          console.log(res)
          this.startSendMsg(300, 'forgetForm')
        })
      },
      sendMsg_infoForm() {
        if (!this['infoForm'].phone) {
          return this.$Notice.error({
            title: ''
          })
        }
        this.action({
          module: `sendMsg_${'infoForm'}`,
          api: `sendMsg_${'infoForm'}`,
          mobile: this['infoForm'].phone,
        }).then(res => {
          console.log(res)
          this.startSendMsg(300, 'infoForm')
        })

      },
      startSendMsg(time, form) {
        if (time == 0) {
          return this[form].sendMsg = ''
        }
        this[form].sendMsg = time / 1
        this[form].sendInterval = setInterval(() => {
          this[form].sendMsg--
            if (this[form].sendMsg == 0) {
              this[form].sendMsg = ''
              return clearInterval(this[form].sendInterval)
            }
        }, 1000)
      },
      login() {
        this.action({
          module: 'Login_login',
          api: 'login',
          params: {
            json: {
              UserName: this.userName,
              UserPwd: this.password,
              VCode: this.verifyCode,
            }
          }
        }).then(res => {
          console.log(res)
          if (res.Res == 1) {
            if (window.$$tokenData == 1) {
              this.infoModal = true
              this.action({
                module: 'Login_getMsgTime',
                api: 'getMsgTime_infoForm'
              }).then(res => {
                if (res.Msg != 0) {
                  this.startSendMsg(res.Msg / 1, 'infoForm')
                }
              })
            } else {
              this.skip()
            }
          }
        })
      },
      refreshVerify() {
        this.verifyImgUrl = `${window.$$baseURL}/APILogin/ValidateCode?${new Date().getTime()}`
      },
      skip() {
        return this.$router.push({
          name: '1-1'
        })
      }
    }
  }

</script>
<style lang="less" scoped>
  .formItem {
    margin-bottom: 20px;
    .inputItem {
      .input {
        width: 50%;
      }
    }
  }

  .formFooter {
    text-align: center;
  }

  .footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: transparent;
    height: 120px;
    padding: 0 10%;
    z-index: 2;
  }

  .logoBox {
    height: 80px;
    padding-top: 10px;
  }

  .loginContainer {
    position: relative;
    height: 100%;
  }

  .loginBox {
    position: absolute;
    top: 15%;
    right: 16%;
    width: 350px;
    height: 400px;
    background: #fff;
    border: 1px solid lighten(#4a7594, 40%);
    box-shadow: 0 5px 20px lighten(#4a7594, 40%);
    padding: 20px 30px;
    z-index: 3;
  }

  .inputBox {
    position: relative;
    margin-bottom: 30px;
    &:hover {
      .iconfont {
        color: #214555
      }
    }
    .iconfont {
      position: absolute;
      left: 5px;
      top: 5px;
      font-size: 22px;
      z-index: 2;
      color: #bbb;
    }
    .input {
      width: 100%;
      height: 40px;
      border: 0;
      outline: none;
      border-bottom: 1px solid #ddd;
      padding-left: 40px;
      font-size: 16px;
      &:focus {
        border-bottom: 1px solid #214555
      }
    }
  }

  .verifyInput {
    float: left;
    height: 30px;
    border: 1px solid #ccc;
    width: 80px;
    text-align: center;
    margin-right: 20px;
  }

  .verifyImg {
    float: left;
    height: 30px;
  }

  .refresh {
    margin-left: 10px;
    line-height: 30px;
    cursor: pointer;
    &:hover {
      color: #999;
    }
  }

  .buttonBox {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    .login {
      width: 100%;
      border-radius: 0;
      height: 50px;
      font-size: 18px;
    }
  }

  .forgetPsw {
    text-align: right;
    line-height: 50px;
    .iconfont {
      position: relative;
      top: 1px;
      left: -2px;
    }
  }

</style>



