<template>
  <div id="app">
    <GlobalProgress ref="GlobalProgress"></GlobalProgress>
    <router-view></router-view>
  </div>
</template>

<script>
import GlobalProgress from '@/components/GlobalProgress'
import {mapActions} from 'vuex'

export default {
  name: 'App',
  components: {
    GlobalProgress
  },
  methods: {
    ...mapActions(['action'])
  },
  created() {
    window.$$Notice = this.$Notice
    window.$$router = this.$router
    window.$$action = this.action
  },
  mounted() {
    window.$$Progress = this.$refs.GlobalProgress
  }
}
</script>

<style lang="less">
@import './theme/main.less';
</style>




