<template>
  <div class="newsContainer">
    <div class="newsBox">
      <TitleLink name=""></TitleLink>
      <li v-for="item in data" class="clearBoth" @click="openNews(item)">
        <div class="circleIcon"></div>
        <div class="articleTitle">
          {{item.ArticleTitle}}
        </div>
        <div class="articleTime">
          {{item.AnnounceShortTime}}
        </div>
      </li>
    </div>
    <Modal
      v-model="modal"
      width="800"
     >
     <h2 slot="header">{{detail.title}} <small>{{detail.time}}</small></h2>
     <div class="articleDetail">
      <div class="articleContent" v-html="detail.content"></div>
     </div>
      
    </Modal>
  </div>
</template>
<script>
  import TitleLink from '@/components/TitleLink'
  export default {
    name: 'News',
    components: {
      TitleLink
    },
    mounted() {
      this.getNewsList()
    },
    data() {
      return {
        data: [],
        modal: false,
        detail: {
          title: '',
          time: '',
          content: ''
        }
      }
    },
    methods: {
      getNewsList() {
        window.$$action({
          module: 'index_getNewsList',
          api: 'index_getNewsList'
        }).then(res => {
          this.data = res.Msg
          console.log(res)
        })
      },
      openNews(item) {
        window.$$action({
          module: 'index_getNewsDetail',
          api: 'index_getNewsDetail',
          params: {
            id: item.id
          }
        }).then(res => {
          this.detail.title = res.Msg.ArticleTitle
          this.detail.time = res.Msg.AnnounceTime
          this.detail.content = res.Msg.ArticleContent
          this.modal = true
        })
      }
    }
  }

</script>
<style lang="less">
.articleContent {
  img {
    max-width: 100%;
  }
}
</style>

<style lang="less" scoped>

.articleDetail {
  padding: 20px 30px;
}
  .newsContainer {
    padding: 0px 15px;
    min-height: 100%;
    font-size: 14px;
    .newsBox {
      background-color: #fff;
      padding: 10px 30px 20px;
    }
    .circleIcon {
      float: left;
      width: 10px;
      height: 10px;
      background-color: #999;
      border-radius: 5px;
      position: relative;
      top: 6px;
      margin-right: 8px;
    }
    .articleTitle {
      float: left;
    }
    .articleTime {
      float: right;
    }
    li {
      margin: 10px 0;
      list-style: none;
      cursor: pointer;
      &:hover {
        color: #999
      }
    }
  }

</style>



