<template>
  <div>
    <Breadcrumb separator=">">
          <BreadcrumbItem v-for="item in data" :key="item.to" :to="item.to">
            {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
  </div>
</template>

<script>
export default {
  name: 'MkBreadcrumb',
  data() {
    return {
      data: [
        {
          to: '/admin/index',
          name: ''
        }
      ]
    }
  },
  mounted() {
    console.log(this)
  }
}
</script>




