<template>
  <div class="titleLinkBox">
    {{name}}
    <router-link v-if="link!=undefined" :to="link" class="linkBox">
       >>
    </router-link>
  </div>
</template>
<script>
export default {
  name: 'TitleLink',
  props: {
    name: null,
    link: null
  }
}
</script>
<style lang="less" scoped>
.titleLinkBox {
  height: 50px;
  line-height: 50px;
  padding: 0 15px;
  font-size: 16px;
  border-bottom: 1px solid #eee;
  .linkBox {
    float: right;
    font-size: 12px;
    color: #999;
    &:hover {
      color: #bbb
    }
  }
}
</style>



