import axios from 'axios'
import _ from 'lodash'
import logout from './actions/logout'
import setToken from './actions/setToken'

window.$$baseURL = 'http://192.168.12.56:8002'

const fetch = axios.create({
  baseURL: window.$$baseURL,
  timeout: 5000,
  withCredentials: true,
})

export function action(config) {
  return manageToken(config).then(config => {
    window.$$Progress.start()
    return fetch(config).then(res => {
      if (res.data.Res == 1) {
        window.$$Progress.finish()
        return res.data
      }
      if (res.data) {
        return Promise.reject(res.data.Err)
      }
      return Promise.reject("")
    }).catch(err => {
      window.$$Notice.error({
        title: err
      })
      window.$$Progress.wrong()
      return Promise.reject(err)
    })
  })
}

function manageToken(config) {
  if (!config.noToken) {
    const token = window.localStorage.getItem('token')
    const tokenDataStr = window.localStorage.getItem('tokenData')
    if (!token || !tokenDataStr) {
      window.$$Notice.error({
        title: ''
      })
      logout()
      return Promise.reject('')
    }
    const tokenData = JSON.parse(tokenDataStr)
    const nowDate = new Date().getTime() / 1000 + 60
    if (tokenData.exp < nowDate) {
      window.$$Notice.error({
        title: ''
      })
      logout()
      return Promise.reject('')
    }

    _.set(config, 'headers.Authorization', token)

    if (tokenData.exp < (nowDate + 1200) && !config.noRefreshToken) {
      return refreshToken(config).then(res => {
        const token = res.Msg
        setToken(token)
        _.set(config, 'headers.Authorization', token)
        return config
      })
    }
  }
  return Promise.resolve(config)
}

function refreshToken({headers}) {
  return action({
    method: 'post',
    url: '/APILogin/GetNewToken',
    noRefreshToken: true,
    headers
  })
}



