import apis from '@/axios/index'
import _ from 'lodash'
import Vue from 'vue'

const initialState = {}

const getters = {
  store: state => state
}

const actions = {
  action ({commit, state}, config) {
    if (!config.module) {
      return console.error('no module')
    }
    if (config.api) {
      return apis[config.api](config).then(res=> {
        if (_.isArray(res)) {
          config.goods = _.merge({}, config.goods, {_data: res})
        } else {
          config.goods = _.merge({}, config.goods, res)
        }
        commit('add', config)
        return res
      })
    }
    return commit('add', config)
  }
}

const mutations = {
  add(state, config) {
    if (config.update) {
      Vue.set(state, config.module, _.extend({}, state[config.module], config.goods))
    } else {
      Vue.set(state, config.module, _.merge({}, state[config.module], config.goods))
    }
  }
}

export default {
  state: initialState,
  getters,
  actions,
  mutations
}



